.hero-unit {
  background-image: url("../images/hero-unit-background-desktop.jpg");
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  padding: 50px 0;

  .hero-unit-subheading {
    color: #9acff6;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 2.4px;
    margin-bottom: 45px;
  }
}

@media only screen and (max-width: 576px) {
  .hero-unit {
    text-align: center;
    background-image: url("../images/hero-unit-background-mobile.jpg");
    background-size: cover;
    background-position: center;
    .hero-unit-heading {
      font-size: 35px;
    }
    .hero-unit-subheading {
      font-size: 14px;
    }
  }
}
